export default {
	onReady() {
		this.requestUrl()
	},
	data() {
		return {
			price: '0'
		}
	},
	methods:{
		bindCode(){
			uni.navigateTo({
				url: '../business/business-code/business-code?id=' + 1
			})
		},
		requestUrl(){
			uni.getStorage({
				key: 'openId',
				success:res=>{
					uni.showLoading({
						title: '读取数据中...',
						mask: false
					})
					let openId = res.data
					uni.request({
						url: 'https://www.finetwm.com/xmRepair/userInfo/findMoney',
						method: 'GET',
						header:{
							'content-Type': "application/x-www-form-urlencoded"
						},
						data: {
							openId: openId
						},
						success: res => {
							console.log(res)
							uni.hideLoading()
							this.price = res.data.data.money
							if(this.price == null){
								this.price = '0'
							}
						},
						fail: err => {
							console.log(err)
						}
					})
				}
			})
			
		}
	}
}
